<template>
    <div class="clearfix h-full">
        <div class="panel-nav">
            <el-breadcrumb separator="/" class="m-l-lg">
                <el-breadcrumb-item>
                {{$t('apply.title')}}
                </el-breadcrumb-item>
            </el-breadcrumb>
        </div>
        <div class="panel-body-list clearfix">
            <list-table
                :data="listData"
                :fields="fields"
                :operators="operators"
                class="wrapper-md"
                :operatorsTitle="$t('apply.operate.title')"
                :isSearchLeft="false">
                <div slot="btns-left">
                    <button class="btn btn-transform padder" @click="agreeItems">{{$t('apply.operate.agree')}}</button>
                    <button class="btn btn-transparent padder m-l-xs" @click="refuseItems">{{$t('apply.operate.refuse')}}</button>
                </div>
                <div slot="btns">
                    <button class="fa fa-refresh btn-refresh" @click="refresh"></button>
                </div>
            </list-table>
        </div>
    </div>
</template>
<script>
    import listTable from '../table/table'

    export default {
        created() {
            this.getApplyList()
        },
        data() {
            return {
                listData: [],
                fields: [{
                    name: "username",
                    showName: this.$t('apply.username')
                }, {
                    name: "name",
                    showName: this.$t('apply.name')
                }, {
                    name: "cellphone",
                    showName: this.$t('apply.cellphone')
                }, {
                    name: "email",
                    showName: this.$t('apply.email')
                }],
                operators: [{
                    name: this.$t('apply.operate.agree'),
                    handler: this.agree
                }, {
                    name: this.$t('apply.operate.refuse'),
                    handler: this.refuse
                }]
            }
        },
        methods: {
            getApplyList() {
                console.log(123)
                this.$httpMethods.lessonGroup.queryAllApply(1).then(res => {
                    if(res && res.status === 200) {

                    }
                })
            },
            refresh() {

            },
            agree() {

            },
            agreeItems() {

            },
            refuse() {

            },
            refuseItems() {

            }
        },
        components: {
            listTable
        }
    }
</script>
<i18n>
    {
        "zh": {
            "apply": {
                "title": "审批",
                "username": "用户名",
                "name": "姓名",
                "cellphone": "手机号码",
                "email": "电子邮箱",
                "operate": {
                    "title": "操作",
                    "agree": "同意",
                    "refuse": "拒绝"
                }
            }
        }
    }
</i18n>
<style scoped>
    
</style>


